.small_cart {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 70px;
    height: 70px;
    font-size: 55px;
    background-color: rgb(128, 128, 128);
    background-image: linear-gradient(to top, #fddb92 0%, #d1fdff 100%);
    text-align: center;
    line-height: 50px;
    border-radius: 5px;
}
.imgs {
  width: 100%;
}
.head-example {
  position: absolute;
  right: 0;
  bottom: 0px;
}
.inside-cart {
  font-size: 30px;
  text-align: center;
}
.content-title {
  position: relative;
  display: inline-block;
}
.badge-inside {
  position: absolute;
  bottom: 0%;
  right: 50%;
}
.cart_right {
  text-align: right;
}
.exit {
  cursor: pointer;
}
.cart-title {
  font-size: 20px;
  padding-left: 10px;
}
.total {
  font-size: 25px;
  font-weight: 500;
  padding-left: 20px;
}
.cart-good-name {
  height: 45%;
}
.cart-good-price {
  font-size: 18px;
  padding-top: 20%;
  padding-right: 5%;
}
.cart-good-price-box {
  height: 46%;
  background-color:azure;
}

